.web-rtc {
  max-width: 1553px;
  margin: 0 auto;
  padding: 40px;
  display: grid;
  grid-template-columns: 200px 4fr 400px;
}
.web-rtc .chat {
    padding: 35px 0;
    grid-column: 3/4;
}
.web-rtc .chat img {
      width: 200px;
}
.web-rtc .chat .chat-img-div {
      width: 100px;
}
.web-rtc .chat .chat-img-div[fullscreen] {
        background: #000;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        position: fixed;
        z-index: 100;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
}
.web-rtc .chat .chat-img {
      width: 100%;
}
.web-rtc .chat .file-icon {
      font-size: 50px;
      color: grey;
}
.web-rtc .chat .file {
      width: 25px;
      opacity: 0.8;
      margin-left: 10px;
}
.web-rtc .chat .chat-file {
      text-align: left;
      min-width: 160px;
}
.web-rtc .chat .input-file {
      opacity: 1;
      position: absolute;
      width: 30px;
      opacity: 0;
      left: 4px;
}
.web-rtc .chat .chat-area {
      width: 100%;
      height: 100px;
      outline: none;
      border: none;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      resize: none;
      padding: 5px;
      color: #333;
      overflow: auto;
      word-break: break-all;
}
.web-rtc .chat .chat-content {
      border: 1px solid #ccc;
      border-top: none;
      position: relative;
}
.web-rtc .chat .chat-content .button-div {
        text-align: right;
        padding: 5px;
}
.web-rtc .chat .chat-content button {
        border: none;
        outline: none;
        background: #67c23a;
        padding: 5px 20px;
        border-radius: 4px;
        color: #fff;
        cursor: pointer;
}
.web-rtc .chat .content {
      height: 400px;
      border: 1px solid #ccc;
      padding: 4px 10px;
      overflow: auto;
}
.web-rtc .chat .msg-content {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: start;
          -ms-flex-pack: start;
              justify-content: flex-start;
      margin: 5px 20px 0 30px;
