}
.web-rtc .chat li {
      margin-bottom: 20px;
}
.web-rtc .chat li.is-self {
        text-align: right;
}
.web-rtc .chat li.is-self .msg-content {
          -webkit-box-pack: end;
              -ms-flex-pack: end;
                  justify-content: flex-end;
