      margin: 5px;
      position: relative;
}
.web-rtc .rtc-room {
    height: calc(100vh - 90px);
}

.rtc-room .el-dialog {
  min-width: 320px !important;
}
.rtc-room .rooms {
  border: 1px solid #ccc;
  min-height: 300px;
}
.rtc-room .rooms li {
    padding: 5px 10px;
    border-radius: 3px;
    height: 40px;
    cursor: pointer;
    position: relative;
}
.rtc-room .rooms li .lock {
      width: 30px;
      position: absolute;
      right: 5px;
      top: 10px;
}
.rtc-room .rooms li:hover, .rtc-room .rooms li.picked {
      background: #ccc;
      color: #fff;
}
.rtc-room .rooms li:hover .tips, .rtc-room .rooms li.picked .tips {
        color: #fff;
}
.rtc-room .name {
  font-size: 16px;
}
.rtc-room .tips {
  font-size: 12px;
  color: #999;
}
.rtc-room .btns {
