  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.rtc-room .btns .el-button + .el-button {
    margin-left: 0;
}
.rtc-room input {
  height: 30px;
}
.rtc-room .v-row {
  grid-template-columns: 70px 1fr;
}
.rtc-room .el-dialog {
  max-width: 600px;
  min-width: 360px;
  width: 80%;
}
.rtc-room .room-tips {
  margin-top: 20px;
}

.v-input {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.v-input input {
    width: 100%;
    height: 40px;
    padding: 0 20px 0 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 4px;
    outline: none;
    border: 1px solid #ccc;
}
.v-input input:focus {
      border: 1px solid #409eff;
}
.v-input input.error {
      border: 1px solid #f56c6c;
}
.v-input .err-tips {
    position: absolute;
